import React from 'react';
import {Link, graphql, StaticQuery} from 'gatsby';
import PreviewCompatibleImage from './PreviewCompatibleImage';
import {Image} from '../models/Image';
import Posts from './components/posts/posts';
import _ from 'lodash';
import Trophaensammlung from './components/trophaensammlung/trophaensammlung';
import {IPost} from '../templates/models/IPost';

class BlogRoll extends React.Component<BlogRollProps> {
    render() {
        return (
            <>
                <Trophaensammlung posts={this.props.data.allMarkdownRemark.edges}/>
            </>
        );
    }
}

interface BlogRollProps {
    data: BlogRollData;
    count: number;
}

interface BlogRollData {
    allMarkdownRemark: {
        edges: IPost[];
    };
}


const renderMethod: any = (data: BlogRollData, count: number) => <BlogRoll data={data} count={count}/>;

export default () => (
    <StaticQuery
        query={graphql`
      query BlogRollQuery {
        allMarkdownRemark(
          sort: { order: DESC, fields: [frontmatter___date] }
          filter: { frontmatter: { templateKey: { eq: "blog-post" } } }
        ) {
          edges {
            node {
              excerpt(pruneLength: 400)
              id
              fields {
                slug
              }
              frontmatter {
                title
                templateKey
                date(formatString: "MMMM DD, YYYY")
                tags
                featuredpost
                featuredimage {
                  childImageSharp {
                    fluid(maxWidth: 1000, quality: 100) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
      }
    `}
        render={renderMethod}
    />
);
